
.article {
  background: #f5f5f5;
  padding: 10px 30px;
  margin: 20px auto;
}
.article h2 {
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #ddd;
}
.article li {
  height: 50px;
  line-height: 50px;
  border-bottom: 1px dotted #ddd;
  cursor: pointer;
  padding: 0 20px;
}
.article li:hover {
  background: #fff;
}
.article li .time {
  color: #666;
  padding-left: 30px;
  font-size: 12px;
  float: right;
}

.article-detail {
  background: #f5f5f5;
  padding: 10px 30px;
  margin: 20px auto;
}
h2 {
  margin-top: 20px;
  font-size: 16px;
}
.tag {
  margin-top: 20px;
  color: #666;
  display: block;
}
img {
  max-width: 400px;
  border: 1px solid #ddd;
  margin-top: 20px;
}
p {
  text-indent: 2em;
  line-height: 30px;
}
.content {
  margin-top: 20px;
}

.about section {
  background: #f5f5f5;
  padding: 10px 30px 30px 30px;
  margin: 20px auto;
}
.about h2 {
  height: 50px;
  line-height: 50px;
}
.about p {
  text-indent: 2em;
}

