.login .box span {
  text-align: left;
  width: 100%;
  color: #494d50;
  font-size: 12px;
}
.login .box input {
  width: 98%;
  background: #f1f5f6;
  border: 1px solid #b8bcbd;
  height: 30px;
  line-height: 30px;
  border-radius: 3px;
  margin: 5px 0 10px 0;
  padding-left: 2%;
}
.login .box button {
  background: #f1f5f6;
  border: 1px solid #b8bcbd;
  height: 30px;
  line-height: 30px;
  border-radius: 3px;
  width: 100px;
  margin-top: 10px;
}
.login .box button[disabled] {
  cursor: not-allowed;
